* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background: #103c42;
}

body {
  max-width: 1500px;
  width: 100%;
  margin: auto;
  font-family: "Share Tech Mono", monospace;
}

.Header {
  margin: 13vh 50px 50px 50px;
  color: #e7e7e7;
  height: 85vh;
}

.Header h1 {
  font-weight: 300;
  font-size: 45px;
  margin-bottom: 10vh;
}

.Header h1 span.wave {
  -webkit-animation-name: wave-animation;
          animation-name: wave-animation;
  -webkit-animation-duration: 2.5s;
          animation-duration: 2.5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  transform-origin: 70% 70%;
  display: inline-block;
}

@-webkit-keyframes wave-animation {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(-10deg);
  }
  20% {
    transform: rotate(12deg);
  }
  30% {
    transform: rotate(-10deg);
  }
  40% {
    transform: rotate(9deg);
  }
  50% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes wave-animation {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(-10deg);
  }
  20% {
    transform: rotate(12deg);
  }
  30% {
    transform: rotate(-10deg);
  }
  40% {
    transform: rotate(9deg);
  }
  50% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

.Header h2 {
  font-weight: 300;
  font-size: 40px;
  max-width: 900px;
  margin-bottom: 10vh;
  line-height: 1.5;
}

.Header h2 h3 {
  font-weight: 300;
  font-size: 40px;
  margin-top: 15px;
}

.Header p {
  font-size: 30px;
}

.Header p a {
  text-decoration: none;
  color: #05a19c;
  transition: 0.3s ease-in-out all;
}

.Header p a:hover {
  font-weight: 900;
}

.Header .content {
  position: absolute;
  overflow: hidden;
  margin-top: 20px;
  font-family: "Share Tech Mono", monospace;
  font-size: 35px;
  line-height: 40px;
  color: #fdcb6e;
}

.Header .content__container {
  font-weight: 600;
  overflow: hidden;
  height: 40px;
  padding: 0 40px;
}

.Header .content__container:before {
  content: "[";
  left: 0;
}

.Header .content__container:after {
  content: "]";
  position: absolute;
  right: 0;
}

.Header .content__container:after, .Header .content__container:before {
  position: absolute;
  top: 0;
  color: #16a085;
  font-size: 35px;
  line-height: 40px;
  -webkit-animation-name: opacity;
  -webkit-animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
  animation-name: opacity;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}

.Header .content__container__text {
  display: inline;
  float: left;
  margin: 0;
}

.Header .content__container__list {
  margin-top: 0;
  text-align: left;
  list-style: none;
  -webkit-animation-name: change;
  -webkit-animation-duration: 10s;
  -webkit-animation-iteration-count: infinite;
  animation-name: change;
  animation-duration: 10s;
  animation-iteration-count: infinite;
}

.Header .content__container__list__item {
  line-height: 40px;
  margin: 0;
}

@-webkit-keyframes opacity {
  0%,
  100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}

@-webkit-keyframes change {
  0%,
  12.66%,
  100% {
    transform: translate3d(0, 0, 0);
  }
  16.66%,
  29.32% {
    transform: translate3d(0, -25%, 0);
  }
  33.32%,
  45.98% {
    transform: translate3d(0, -50%, 0);
  }
  49.98%,
  62.64% {
    transform: translate3d(0, -75%, 0);
  }
  66.64%,
  79.3% {
    transform: translate3d(0, -50%, 0);
  }
  83.3%,
  95.96% {
    transform: translate3d(0, -25%, 0);
  }
}

@keyframes opacity {
  0%,
  100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}

@keyframes change {
  0%,
  12.66%,
  100% {
    transform: translate3d(0, 0, 0);
  }
  16.66%,
  29.32% {
    transform: translate3d(0, -25%, 0);
  }
  33.32%,
  45.98% {
    transform: translate3d(0, -50%, 0);
  }
  49.98%,
  62.64% {
    transform: translate3d(0, -75%, 0);
  }
  66.64%,
  79.3% {
    transform: translate3d(0, -50%, 0);
  }
  83.3%,
  95.96% {
    transform: translate3d(0, -25%, 0);
  }
}

@media only screen and (max-width: 1024px) {
  .Header h1 {
    font-size: 40px;
  }
  .Header h2 {
    font-size: 35px;
  }
  .Header h2 h3 {
    font-size: 35px;
    margin-top: 25px;
  }
  .Header p {
    font-size: 25px;
  }
  .Header .content {
    font-size: 30px;
  }
}

@media only screen and (max-width: 768px) {
  .Header h1 {
    font-size: 37px;
  }
  .Header h2 {
    font-size: 32px;
  }
  .Header h2 h3 {
    font-size: 32px;
    margin-top: 25px;
  }
  .Header p {
    font-size: 25px;
  }
  .Header .content {
    font-size: 30px;
  }
}

@media only screen and (max-width: 500px) {
  .Header h1 {
    font-size: 32px;
  }
  .Header h2 {
    font-size: 29px;
  }
  .Header h2 h3 {
    font-size: 29px;
  }
  .Header p {
    font-size: 23px;
  }
  .Header .content {
    font-size: 25px;
  }
}

@media only screen and (max-width: 375px) {
  .Header h1 {
    font-size: 25px;
  }
  .Header h2 {
    font-size: 22px;
  }
  .Header h2 h3 {
    font-size: 22px;
  }
  .Header p {
    font-size: 20px;
  }
  .Header .content {
    font-size: 21px;
  }
}

.Background {
  display: flex;
  justify-content: space-around;
  max-width: 1200px;
  margin: auto;
}

.Background .Bg-Header {
  color: #05a19c;
  font-weight: 900;
  font-size: 22px;
  margin: 15px;
  letter-spacing: 3px;
}

.Background .Bg-Container {
  color: #aaaaaa;
  max-width: 950px;
  line-height: 1.5;
  font-size: 22px;
  margin: 15px;
}

.Background .Bg-Container a {
  text-decoration: none;
  color: #05a19c;
  font-weight: 900;
  font-size: 20px;
}

.Background .Bg-Container a:hover {
  border-bottom: 2px solid #05a19c;
}

@media only screen and (max-width: 767px) {
  .Background {
    display: flex;
    flex-direction: column;
    margin: 0 30px;
  }
}

.Skills {
  display: flex;
  justify-content: space-around;
  max-width: 1200px;
  margin: auto;
}
